//导入路由对象
import VueRouter from 'vue-router'
import Vue from 'vue'


//通过Vue.use() 安装插件
Vue.use(VueRouter)
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}

const Home = () => import('../components/home/Home')
const Shopping = () => import('../components/shopping/Shopping')
const List = () => import('../components/list/List')
const Mine = () => import('../components/mine/Mine')
const Detial = () => import('../components/detial/Detial')

//创建路由对象
const routes = [
    {
        path: '',
        redirect: '/home'
    },
    {
        path: '/home',
        component: Home
    },
    {
        path: '/shopping',
        component: Shopping
    },
    {
        path: '/mine',
        component: Mine
    },
    {
        path: '/list',
        component: List
    },
    {
        path: '/detail/:iid',
        component: Detial
    }
]
const router = new VueRouter({
    routes,
    //mode: 'history', //默认为hash模式修改为history模式即可
    linkActiveClass: 'active'  //修改默认点击添加的类名名字

})

//将router对象传递给vue实例
export default router

