export default {
  addCart(context, payload) {
    //新的商品是否已经添加过
    let oldproduct = null;
    for(let item of context.state.cartList) {
      if(item.iid == payload.iid) {
        oldproduct = item
      }
    }
    if(oldproduct) {
      context.commit('addcounter', oldproduct)
    } else {
      payload.count = 1
      //context.state.cartList.push(payload)
      context.commit('addToCart', payload)
    }      
  }
}