<template>
  <div class="bottom-bar-item" @click="tabbarClick">
    <div><slot name="item-img"></slot></div>    
    <div :style="activeStyle">
      <slot name="item-text"></slot>
    </div>
  </div>
</template>
<script>
export default {
  name: "Bottombaritem",
  props: {
    path: String,
    activeColor: {
      type: String,
      default: 'red'
    }
  },
  data() {
    return {      
    }
  },
  computed: {
    isActive() {
      return this.$route.path.indexOf(this.path) !== -1
    },
    activeStyle() {
      return this.isActive ? {color: this.activeColor} : {}
    }
  },
  methods: {
    tabbarClick() {
      if(this.$route.path.indexOf(this.path) === -1) {
        this.$router.replace(this.path)          
      }         
    }
  }
};
</script>

<style>
.bottom-bar-item {
  flex: 1;
  text-align: center;
}
</style>
